import { getPlaylistMore } from "api/find.js"
const LIMIT = 21;
const ORDER = "new";
export default {
	data(){
		return{
		
			playlistmore : null,	
		} 
	},
	props:{
		playlistdetailmore: {},
		
		
	},
	onLoad(options) {
		this.id = options.id;
		this.init();
		
	},
	methods: {
		init(){
			this.handlePlaylist();
	
		},
		async handlePlaylist(){
			let reqdata = {};
			reqdata.limit = LIMIT;
			reqdata.order = ORDER;
			this.playlistmore = await getPlaylistMore(reqdata);		
		},
		clickplaylist(id) {
			uni.navigateTo({
				url:`../playList/playlistdetail?id=${id}`
				//等同于'../find/playList/playlistdetail?id=' + id 反撇号简化了拼接
			
			});
		},
				
	},
	
	
}
	
